@use './../../../styles/imports' as *;
@use 'sass:map';

$this-block: 'tabs';

.#{$this-block} {
  border: var(--border-width) solid var(--color-gray-5);
  border-radius: map.get($border-radius, 'lg');
  &-header,
  &-body,
  &-toggle {
    display: none;
  }
  &-body {
    padding: map.get($gap-sizes, 5);
  }
  &-item-active {
    .#{$this-block}-body {
      display: block;
    }
  }
  &-toggles {
    align-items: flex-start;
    display: flex;
    gap: var(--border-width);
    justify-content: flex-start;
    margin-bottom: var(--border-width);
    overflow-x: auto;
    white-space: nowrap;
    width: 100%;
  }
  &-button {
    background-color: transparent;
    border-radius: map.get($border-radius, 'lg');
    border: var(--border-width) solid var(--color-gray-5);
    color: inherit;
    cursor: pointer;
    padding: map.get($gap-sizes, 5);
    position: relative;
    text-align: left;
    vertical-align: top;
    width: auto;
    &-active {
      background-color: var(--color-active-state);
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
      font-family: inherit;
      font-size: inherit;
      font-weight: inherit;
      margin: 0;
    }
  }
}
